@font-face {
  font-family: "BebasNeue";
  src: url("../fonts/BebasNeue.woff2") format("woff2"), url("../fonts/BebasNeue.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap; }

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/Roboto-Condensed-Regular.woff2") format("woff2"), url("../fonts/Roboto-Condensed-Regular.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap; }

img {
  width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: inherit; }

body {
  background-color: #B8B8B8; }

.body-container {
  display: flex;
  justify-content: center; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.landing-block {
  width: 1366px;
  min-height: 658px;
  font-family: "BebasNeue", "Arial", sans-serif;
  background-image: url("../img/landing-background.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  color: #fff; }

.landing-block__top-line {
  display: flex;
  padding-top: 3px;
  height: 48px; }

.landing-block__name {
  width: 89px;
  font-size: 26px;
  line-height: 26px;
  text-transform: uppercase;
  margin-left: 103px;
  letter-spacing: 0.02em; }

.landing-block__phone {
  width: 154px;
  font-size: 26px;
  line-height: 26px;
  margin-left: auto;
  margin-right: 103px;
  letter-spacing: 0.03em; }

.landing-block__wrapper {
  display: flex; }

.landing-block_text-block {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 36.5%; }

.landing-block__image-container {
  width: 500px;
  height: 556px; }

.landing-block__city {
  margin: 0;
  margin-top: 118px;
  margin-bottom: 7px;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em; }

.landing-block__title {
  margin: 0;
  text-transform: uppercase;
  text-align: right;
  font-size: 109px;
  font-weight: 700;
  letter-spacing: 0.03em;
  line-height: 109px;
  width: 298px;
  margin-bottom: 61px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }

.landing-block__image-container {
  margin-left: 64px; }

.landing-block__types-list {
  position: absolute;
  top: 305px;
  left: 122px; }

.landing-block__button {
  width: 232px;
  height: 60px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em; }

.landing-block__social-list {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: 103px;
  margin-top: auto;
  margin-bottom: 21px; }

.types-list {
  list-style: none;
  padding: 0;
  margin: 0;
  text-transform: capitalize;
  text-align: right;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: normal; }

.button {
  background-color: transparent;
  border: 4px solid #fff;
  color: #fff;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 700; }

.social-list {
  list-style: none;
  padding: 0; }

.social-list__item {
  margin-bottom: 20px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px; }
